@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap);
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font: 400 14px 'poppins';
    background-color: #222222;
    color: #D5D5D5;
}

h1 {
    font: 600 40 'poppins';
}

h2 {
    font: 700 25 'poppins';
}

.container {
    margin-left: auto;
    margin-right: auto;

    max-width: 1200px;
    width: 100%;
    height: 100vh;
}
.menu {
    display: flex;
    align-items: center;
    justify-content: space-between;

    width: 100%;
    height: 70px;
}

.menu p {
    font: 600 20px 'poppins';
    color: #f5f5f5;
}

.menu ul {
    list-style-type: none;
}

.menu ul li {
    font: 500 13px 'poppins';
    color: #d5d5d5;
    display: inline;
    margin-left: 100px;
}

.menu ul li a {
    font: 500 13px 'poppins';
    color: #d5d5d5;
    text-decoration: none;
    transition: 0.5s;
}

.menu ul li a:hover {
    color: #aaaaaa;
}

@media screen and (max-width: 1200px) {
    .menu p {
        font-size: 15px;
        margin-left: 10px;
    }

    .menu ul li {
        font: 500 13px 'poppins';
        color: #d5d5d5;
        display: inline;
        margin-left: 20px;
        margin-right: 10px;
    }
}
.footer {
    display: flex;
    align-items: center;
    justify-content: space-between;

    width: 100%;
    height: 70px;
    margin-top: 70px;
}
.contact {
    font: 400 16px 'poppins';
    color: #f5f5f5;
}
.copyright {
    font: 500 16px 'poppins';
    color: #d5d5d5;
}

@media screen and (max-width: 1200px) {
    .contact {
        font: 400 9px 'poppins';
        color: #f5f5f5;
        margin-left: 10px;
    }
    .copyright {
        font: 500 9px 'poppins';
        color: #d5d5d5;
        margin-right: 10px;
    }
}
.personal-info {
    display: flex;
    align-items: center;
    justify-content: space-between;

    width: 100%;
    margin-top: 80px;
    padding: 0px 70px 0px 70px;
}

.personal-text {

}

.personal-interests {
    font: 500 16px 'poppins';
    color: #aaaaaa;
}

.personal-description {
    margin-top: 36px;
    font: 400 14px 'poppins';
    color: #d5d5d5;
}

.personal-photo {
    margin-left: 50px;
}

.personal-photo img {
    max-width: 353px;
}

@media screen and (max-width: 1200px) {
    .personal-photo {
        margin-left: 0px;
        margin-top: 50px;
        text-align: center;
    }

    .personal-photo img {
        max-width: 200px;
    }
    
    .personal-info {
        display: block;
    }
}


.skills {
    margin-top: 70px;

    width: 100%;
    margin-top: 80px;
}
.skills p {
    font: 400 14px 'poppins';
    color: #D5D5D5;
    text-align: center;
}

.skills ul {
    list-style-type: none;
    margin-top: 60px;

    max-width: 900px;
    margin-left: auto;
    margin-right: auto;
}

.skills ul li {
    display: inline-table;
    text-align: center;
    
    min-width: 20%;
    font: 500 16 'poppins';
    color: #F5F5F5;

    padding-top: 20px;
    padding-bottom: 20px;
}

.skills ul li span {
    font: 500 30px 'poppins';
    line-height: 1ch;
    color: #F5F5F5;
}


.projects {
    margin-top: 70px;
    width: 100%;
    margin-top: 80px;
}

.project-list {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
}

.project-item {
    width: 33.3%;
    text-align: center;
    padding: 10px;
    min-height: 600px;
}

.project-item p strong {
    color: #E11A3F;
}

.project-item a {
    text-decoration: none;
    color: #F5F5F5;
    transition: 0.5s;;
}
.project-item a:hover {
    color: #aaaaaa;
}

@media screen and (max-width: 1200px) {
    .project-list {
        display: block;
    }
    .project-item {
        width: 100%;
    }
}
